import React from 'react';
import { map, take, isNull, isEmpty, isString } from 'lodash';
import { t } from 'sn-translate';

import canRenderAction from './utils';
import { DECLARATIVE_ACTION_FETCH_FIELD_GRAPHQL } from './actions';

import { transformer } from '../../common/transformActions';

import '@devsnc/sn-record-control';

const createData = (model, { wsConfigSysId }) => {
	const tableName = model.tableName ? model.tableName : 'global';
	const sysId = model.recordSysId ? model.recordSysId : '-1';
	const workspaceConfigId = isString(wsConfigSysId) ? wsConfigSysId : '-1';

	return {
		tableName,
		sysId,
		workspaceConfigId
	};
};

export class Action extends React.PureComponent {
	onClick = () => {
		const { dispatch, action, model } = this.props;
		dispatch('DECLARATIVE_ACTION', { action, model });
	};

	render() {
		const { action, model } = this.props;

		return (
			<sn-record-control
				role="application" //fix JAWS on IE to work with Enter and Spacebar
				icon={action.icon}
				on-click={this.onClick}
				tooltip={action.tooltip}
				config-aria={{
					'aria-label': t(
						'Field action for {0}: {1}: {2}',
						model.label,
						action.label,
						model.displayValue
					)
				}}
			/>
		);
	}
}

class DeclarativeFieldActions extends React.Component {
	componentDidMount() {
		const { dispatch, behaviors } = this.props;

		const { actions, model, fetchDeclarativeActions } = this.props.properties;

		// we can possibly move this type of logic into a behavior
		if (isNull(actions) && fetchDeclarativeActions)
			dispatch(
				DECLARATIVE_ACTION_FETCH_FIELD_GRAPHQL,
				createData(model, behaviors)
			);
	}

	render() {
		const { dispatch, behaviors } = this.props;

		const {
			model,
			actions,
			actionsFromDatasource,
			formData
		} = this.props.properties;

		const resolvedActions =
			isNull(actions) && !isEmpty(actionsFromDatasource)
				? actionsFromDatasource
				: actions;

		const cappedActions = take(resolvedActions, 3);
		const evaluatedActions = transformer(cappedActions, model);
		return map(evaluatedActions, action => {
			if (!canRenderAction({ model, action, formData, behaviors })) {
				return null;
			}

			return (
				<Action
					key={action.name}
					model={model}
					action={action}
					dispatch={dispatch}
				/>
			);
		});
	}
}

DeclarativeFieldActions.defaultProps = {
	properties: {
		model: {},
		actions: null,
		actionsFromDatasource: null,
		fetchDeclarativeActions: false,
		formData: {}
	}
};

export default DeclarativeFieldActions;
